:root {
  --primary-color: #1F58A8;
  --secondary-color: cornflowerblue;
}

html {
  height: 100%;
}

::-moz-selection {
  background: #b3d4fc;
  text-shadow: none;
}

::selection {
  background: #b3d4fc;
  text-shadow: none;
}

hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #ccc;
  margin: 1em 0;
  padding: 0;
}

/*
 * Remove the gap between audio, canvas, iframes,
 * images, videos and the bottom of their containers:
 * https://github.com/h5bp/html5-boilerplate/issues/440
 */

audio,
canvas,
iframe,
img,
svg,
video {
  vertical-align: middle;
}

/*
 * Remove default fieldset styles.
 */

fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}

/*
 * Allow only vertical resizing of textareas.
 */

textarea {
  resize: vertical;
}

.important {
  position: relative;
  color: var(--secondary-color);
}

.important:after {
  content: '';
  display: block;
  background: var(--secondary-color);
  width: 1px;
  height: 1px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
  transition: width .2s ease, height .4s, opacity .1s;
  z-index: -1;
  opacity: 0;
}

.important:hover {
  color: white;
}

.important:hover:after {
  width: calc(100% + 10px);
  height: 100%;
  opacity: 1;
}

h1, h2, h3, h4, h5 {
  font-family: 'Poppins Bold', sans-serif;
  margin-top: 0;
  padding-top: 0;
}

p {
  font-family: 'Poppins', sans-serif;
  margin-top: 0;
  padding-top: 0;
}

body {
  height: 100%;
  display: flex;
  flex-direction: column;
}

header {
  width: 1200px;
  min-height: 90px;
  margin-inline: auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

header ul {
  list-style: none;
}

header ul li {
  display: inline-block;
  font: 18px 'Poppins Bold', sans-serif;
}

header ul li a {
  padding: 10px 4px;
  position: relative;
  color: var(--secondary-color);
  text-decoration: none;
}

header ul li a:after {
  content: '';
  display: block;
  background: var(--secondary-color);
  width: 1px;
  height: 1px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
  transition: width .2s ease, height .4s, opacity .1s;
  z-index: -1;
  opacity: 0;
}

header ul li:hover a {
  color: white;
}

header ul li:hover a:after {
  width: calc(100% + 40px);
  height: 30px;
  opacity: 1;
}

header ul li:not(:last-child) {
  margin-right: 40px;
}

main {
  flex-grow: 1;
}

.hero {
  margin-top: 80px;
  display: flex;
  flex-direction: row;
  gap: 40px;
  height: 60vh;
  width: 1200px;
  margin-inline: auto;
}

.hero .description,
.hero .illustration {
  flex: 50%;
}

.hero h2 {
  font-size: 40px;
}

.hero p {
  font-size: 30px;
  margin: 0 0 40px;
  padding: 0;
}

.hero .description {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
}

.hero .illustration img {
  width: 600px;
}

footer {
  background: var(--primary-color);
  height: 300px;
}

button.primary {
  position: relative;
  color: white;
  border: none;
  padding-inline: 20px;
  padding-block: 10px;
  padding-right: 60px;
  height: 50px;
  background: transparent;
  font: 14px 'Poppins Bold', sans-serif;
}

button.primary:hover {
  cursor: pointer;
}

button.primary:before {
  content: '';
  display: block;
  position: absolute;
  z-index: 1;
  top:0;
  left:0;
  right:0;
  bottom:0;
  background: var(--primary-color);
  clip-path: polygon(3% 0, 100% 0, 97% 100%, 0% 100%);
}

button.primary > span {
  z-index: 1;
  position: relative;
}

button.primary .icon {
  background: var(--secondary-color);
  width: 40px;
  height: calc(100% + 15px);
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: -7px;
  right: 10px;
  z-index: 2;
  clip-path: polygon(14% 0, 100% 0, 86% 100%, 0% 100%);
  transition: all .2s ease;
}

section {
  margin-bottom: 80px;
}

.centered {
  text-align: center;
}

.large {
  font-size: 30px;
}

.container {
  width: 1200px;
  margin-inline: auto;
}

.section-title {
  font-size: 42px;
  text-align: center;
  margin-bottom: 48px;
  position: relative;
}

.separator {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 60px;
}

.separator .icon {
  font-size: 24px;
  margin-inline: 20px;
  color: #B4BEC9;
}

.separator::before,
.separator::after {
  content: '';
  display: block;
  width: 100%;
  background: #B4BEC9;
  height: 1px;
}

.col {
  display: grid;
  column-gap: 40px;
}

.col.col-4 {
  grid-template-columns: auto auto auto auto;
}

.cards .col {
  align-content: baseline;
}

.card .icon {
  font-size: 50px;
  color: var(--secondary-color);
}

.card h3 {
  margin-block: 30px;
  color: var(--secondary-color);
}

.card p {
  font-family: 'Poppins Regular', sans-serif;
}
